.csm_s {
       margin-right: 180px;
       max-width: 350px;
}

.turst_app{
       transition: all .2s ease-out;
}
.turst_app:hover{
       box-shadow: 0 1px 10px #0003;
}

@media screen and (max-width: 767px) {
       .csm_s {
              margin-right: 0;
              max-width: 100%;
              margin-bottom: 30px !important;
       }
   
       .csm_profile {
              right: -20px !important;
       }
   
       .csm_profile img {
              max-width: 130px;
       }
   }
   